@import url(https://fonts.googleapis.com/css?family=Black+Han+Sans|Noto+Sans+KR);
body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans KR", "Ubuntu", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  align-items: center;
  background: linear-gradient(#FF5387, #FF3A75);
  display: flex;
  height: 3rem;
  margin-bottom: 1rem;
  overflow-x: hidden;
}

#nav-logo, .nav-item {
  color: #FFF;
  text-decoration: none;
  text-shadow: 2px 2px 2px #B21746;
}

#nav-logo {
  font-family: "Black Han Sans", "NT Sans KR", serif;
  font-size: 2rem;
  margin: 0 1rem;
}

.nav-item {
  font-weight: 600;
  padding: 4px;
  margin-right: 0rem;
}
#report-form-wrapper {
  align-items: center;
  display: inline-grid;
  grid-template-columns: auto auto;
  grid-auto-rows: auto;
  grid-gap: 1rem;
  padding: 1rem;
}

.report-form-input {
  display: flex;
  flex-direction: column;
}

.report-form-label {
  text-align: right;
}

.report-form-section {
  justify-self: center;
  grid-column: 1 / 3;
}

#report-form-submit {
  background: linear-gradient(#3AFF50, #05B218);
  border: 0;
  border-radius: 2rem;
  box-shadow: none;
  color: #FFF;
  font-size: 1rem;
  font-weight: 600;
  outline: none;
  padding: 1rem;
  text-shadow: 2px 2px 2px #036E0F;
}

select {
  border: 2px solid grey;
  border-radius: 2rem;
  height: 2rem;
  padding: 4px;
}
.list {
  width: 29vw;
  height: 50vw;
  background: pink;
  color: maroon;
  overflow: scroll;
  display: inline-block;
  padding-left: 1vw;
}
.map {
  width: 70vw;
  height: 50vw;
  display: inline-block;
}

